import { Injectable } from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {Usuario} from "../model/usuario";
import {Observable} from "rxjs/Observable";
import {AppResponse} from "../model/app-response";
import {UrlConstants} from "./UrlConstants";
import {catchError} from "rxjs/operators";
import {of} from "rxjs/observable/of";

@Injectable()
export class UsuarioService {

  constructor(private http: HttpClient) { }

  validateInputs(user: Usuario): Observable<any | AppResponse>{
    return this.http.post<AppResponse>(UrlConstants.VALIDATE_USER_INPUTS, user)
      .pipe(
        catchError(this.handleError(UrlConstants.VALIDATE_USER_INPUTS, []))
      );
  }

  saveOrUpdate(user: Usuario): Observable<any> {
    return this.http.post(UrlConstants.SAVE_USER, user);
  }

  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result **/

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      /*// TODO: better job of transforming error for user consumption
       this.log(`${operation} failed: ${error.message}`);*/

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  delete(user: Usuario): Observable<any> {
    return this.http.post(UrlConstants.DELETE_USER, user);
  }

}



