import { Component, OnInit } from '@angular/core';
import {UserFilter} from '../filters/userFilter';
import {EventBusService} from '../../services/event-bus.service';
import { Usuario } from '../model/usuario';
import {environment} from '../../../environments/environment';
import { UrlConstants } from '../services/UrlConstants';
import {UsuarioService} from "../services/usuario.service";
import {AppResponse} from "../model/app-response";
declare var $: any;

@Component({
  selector: 'app-usuario',
  templateUrl: './usuario.component.html',
  styleUrls: ['./usuario.component.css']
})
export class UsuarioComponent implements OnInit {
  userFilter: UserFilter = new UserFilter;
  user: Usuario = new Usuario;
  formMessages: string[] = [];


  constructor(private eventBusService: EventBusService, private usuarioService: UsuarioService) {

   }

  ngOnInit() {
    this.buildTable();
  }

  updateTable() {
    $('#table').DataTable().ajax.reload();
  }


  buildTable() {
    const self = this;
    $.fn.dataTable.ext.errMode = 'none';

    const tablaUsuarios = $('#table').DataTable( {
      columns: [// el atributo data se toma en el server para sacar la columna por la que se ordena
        { data: 'nombre', title: 'Nombre' },
        { data: 'apellido', title: 'Apellido' },
        { data: 'email', title: 'Email' },
        // ejemplo de coleccion
//        { data: 'perfiles', title: 'Perfiles', orderable: false,
//          render: function ( data) {
//            const descripciones = data.map(function(p){
//              return p.descripcion;
//            });
//            return descripciones.join(', ');
//          }},
        { data: 'celular', title: 'Contacto' },
        { data: 'tipoDocumento.descripcion', title: 'Tipo Documento'},
        { data: 'numeroDocumento', title: 'Nro Documento' },
        // ejemplo de indireccion
//        { data: 'seccion.descripcion', title: 'Seccion' },
        { data: 'activo', title: 'Activo',
          render: function(data){
            return data != null && data ? 'Si' : 'No';
          }
        },
        { data: null, title: 'Acciones', defaultContent: self.buildTableButtons(),
          orderable: false, width: 90 }
      ],
      processing: true,
      serverSide: true,
      searching: false,
      scrollX: true,
      ajax: {
        url: environment.apiUrl + UrlConstants.GET_USERS, // environment.apiUrl + UrlConstants.GET_USERS,
        type: 'POST',
        contentType: 'application/json',
      //  beforeSend: function (request) {
    //      request.setRequestHeader('Authorization', self.loginService.getAuthorizationHeader());
    //    },
        data: function(d) {
          // filtro custom agregado al filtro default de datatable
          d.userFilter = self.userFilter;
          // importante para parsear en java se manden o no filtros extras
          return JSON.stringify( d ) ;
        },
        error: function (xhr, error, thrown) {
          if (xhr.status === 401 || xhr.status === 0) {
            self.eventBusService.broadcast('http-error-auth', null);
          }
        }
      },
      language: {
        url: 'assets/datatable/spanish.json'
      },
      // responsive: opcional. Oculta columnas al achicar la pantalla
      responsive: {
            details: {
                display: $.fn.dataTable.Responsive.display.modal( {
                    header: function ( row ) {
                        const data = row.data();
                        return 'Details for ' + data[0] + ' ' + data[1];
                    }
                } ),
                renderer: $.fn.dataTable.Responsive.renderer.tableAll( {
                    tableClass: 'table'
                } )
            }
        }
    } );

    $('#table tbody').on('click', '.edit', function () {
      self.user = tablaUsuarios.row($(this).parents('tr').first()).data();
      self.editUser();
    });

    $('#table tbody').on('click', '.delete', function () {
      self.user = tablaUsuarios.row($(this).parents('tr').first()).data();
      self.showConfirmDialog();
    });
  }

   buildTableButtons(): string {
    let html = '';
//    if (this.loginService.hasPermission(PermisosConstants.USUARIOS_MODIFICACION)) {
      html = "<a title='Editar' class='btn btn-success edit'><span class='fas fa-edit'></span></a>";
//    }
//    if (this.loginService.hasPermission(PermisosConstants.USUARIOS_BAJA)) {
      html = html + "  <a title='Eliminar' class='btn btn-danger delete'><span class='fas fa-trash-alt '></span></a>"
//    }
    return html;
  }

  onClickAceptar(event: any){
    event.preventDefault();
    this.usuarioService.validateInputs(this.user).subscribe(appResponse => this.onSuccess(appResponse));
  }

  onSuccess(appResponse: any){
    if(appResponse.code == AppResponse.SUCCESS){
      this.user.numeroDocumento = +this.user.numeroDocumentoString; // el + lo convierte a numero
      this.usuarioService.saveOrUpdate(this.user).subscribe(response => {
          this.updateTable();
        });
      this.closeModal();
    }else{
      $('#formAlert').show();
      this.formMessages = <string[]> appResponse.data;
    }
  }
/**

<div class="btn-toolbar" role="toolbar" >
  <div class="btn-group" role="group" aria-label="Third group">
    <button type="button" class="btn btn-secondary">8</button>
  </div>
  <div class="btn-group" role="group" aria-label="Third group">
    <button type="button" class="btn btn-secondary">8</button>
  </div>
</div>
 */
   editUser() {
    this.openModal();
  }

  onClickConfirmar(event: any){
    this.usuarioService.delete(this.user).subscribe(
      response => {
        this.updateTable();
        this.user = new Usuario;
      });

    $("#confirmDialog").modal('hide');
  }

  showConfirmDialog(){
    $("#confirmDialog").modal({
      backdrop: 'static',
      keyboard: false,
      show: true
    });
  }

  openModal() {
    $('#userForm').modal({
      backdrop: 'static',
      keyboard: false,
      show: true
    });
  }

  closeModal() {
    $("#userForm").modal('hide');
    this.hideFormAlert();
  }

  hideFormAlert(){
    $('#formAlert').hide();
  }

}



