import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {HTTP_INTERCEPTORS, HttpClient, HttpClientModule} from '@angular/common/http';
import {AppRoutingModule} from './services/app-routing.module';

import { AppComponent } from './app.component';
import { AppNavbarComponent } from './app-navbar/app-navbar.component';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { UsuarioComponent } from './example/usuario/usuario.component';
import { FormsModule } from '@angular/forms';
import { LocationStrategy } from '@angular/common';
import { HashLocationStrategy } from '@angular/common';
import { NgxPermissionsModule } from 'ngx-permissions';
import {EventBusService} from './services/event-bus.service';
import {NotificacionComponent} from './notificacion/notificacion.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import {AppHttpInterceptor} from './services/http.interceptor';
import { PaginaComponent } from './example/pagina/pagina.component';
import { ErrorPageComponent } from './error-page/error-page.component';
import {UsuarioService} from "./example/services/usuario.service";

@NgModule({
  declarations: [
    AppComponent,
    AppNavbarComponent,
    UsuarioComponent,
    NotificacionComponent,
    SidebarComponent,
    PaginaComponent,
    ErrorPageComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    AppRoutingModule,
    NgbModule.forRoot(),
    NgxPermissionsModule.forRoot()
  ],
  providers: [
    { provide: LocationStrategy, useClass: HashLocationStrategy },
    {provide: HTTP_INTERCEPTORS, useClass: AppHttpInterceptor, multi: true},
    EventBusService,
    UsuarioService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



